// Стили для хедера
  .header {
    position: absolute;
    height: 502px;
    background-color:var(--hero-background-color);
    width: 100%;
    z-index: -3;

    @media (min-width:768px) {
      height: 416px;
    }

    @media (min-width:1200px) {
      height: 664px;
    }
  }
  .header::after {
    position: absolute;
    width: 346px;
    height: 344px;
    left: 36px;
    top: 83px;
    background: #FFA5BA;
    content: "";
    border-radius: 50%;
    z-index: -2;
    overflow: hidden;
    position: fixed;

    @media (min-width:768px) {
            position: absolute;
            width: 335px;
            height: 333px;
            left: 248px;
            top: 43px;
        }

        @media (min-width:1200px) {
            position: absolute;
            width: 534px;
            height: 534px;
            left: 517px;
            top: 54px;
        }

  }

  .header::before{
    position: absolute;
    background: url('../images/hero.scss/ice-cream1x.png');
    content: "";
    object-fit: contain;
    width: 171px;
    height: 410px;
    left: 117px;
    top: 92px;
    z-index: -1;
    background-repeat: no-repeat;

    @media (min-width:768px) {
      left: 310px;
      top: 80px;
      width: 143px;
      height: 336px;
      background-size: contain;
    }

    @media (min-width:1200px) {
      width: 236px;
      height: 557px;
      left: 636px;
      top: 108px;
      background-size: contain;
    }

  
  }
  
  .nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 8px;
    @media screen and( min-width: 768px) and(max-width:1199px) {
     padding-top: 32px; 
    }
  }
  .nav__burger-button {
    background-color: transparent;
    z-index: 1;
    border: none;
    @media screen and( min-width: 768px) and(max-width:1199px) {
      margin-right: 32px; 
      margin-left: auto;
     }
  }
  .order-button {
    display: block;
    margin: 60px auto 20px auto;
    width: 209px;
    height: 44px;
    background:var( --primary-white-color);
    border-radius: 22px;
    border-color: transparent;
    padding: 0 48px;
    color: var(--primary-title-color);
    @media screen and( min-width: 768px) and(max-width:1199px) {

    }}
    .order-button--tablet{
     @media screen and (max-width: 767px) {
      display: none;
      
     }
      margin: 0;
    }
  
  .nav__link {
    display: flex;
    width: 100%;
    border-bottom: 1px solid #e18298;
    
    height: 42px;
    align-items: center;
    padding-left: 20px;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    display: flex;
    align-items: center;
    color:var( --primary-white-color);
    &:first-child{
      border-top: 1px solid #e18298;
    }
  }
  
  .mobal-menu {
    position: fixed;
    top: 0;
    // left: 0;
    right: 0;
    bottom: 0;
  
    overflow: hidden;
    transform: translateX(100%);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    background: #e17992;
  }
  .mobal-menu.is-open {
    transform: translateX(0%);
  }
  .nav__burger-button .nav__icon-close {
    display: none;
  }
  .nav__burger-button.is-open {
    & .nav__icon-burger {
      display: none;
    }
    & .nav__icon-close {
      display: block;
    }
  }
  